@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

body {
    margin: 0;
    background: #0d0c1d;
    color: #f8f8f2;
    font-family: 'Arial', sans-serif;
}

.pixel-font {
    font-family: 'Press Start 2P', cursive;
    color: #39ff14;
}

header.header {
    background: #1f1b2e;
    padding: 2rem 1rem;
    text-align: center;
    border-bottom: 4px solid #39ff14;
}

header nav {
    margin-top: 1rem;
}

    header nav a {
        margin: 0 1rem;
        color: #fff;
        text-decoration: none;
        font-weight: bold;
        transition: color 0.3s ease;
    }

        header nav a:hover {
            color: #39ff14;
        }

section {
    padding: 3rem 2rem;
    max-width: 900px;
    margin: 0 auto;
}

.about, .contact {
    background: #1a1927;
    border-left: 5px solid #39ff14;
    padding: 2rem;
    margin-bottom: 2rem;
    box-shadow: 0 0 15px rgba(57, 255, 20, 0.3);
}

.projects {
    background: #181624;
    padding: 2rem;
    border: 2px dashed #39ff14;
    box-shadow: inset 0 0 10px #39ff14;
}

.project-list {
    display: grid;
    gap: 1.5rem;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    margin-top: 1.5rem;
}

.project-card {
    background: #251f3d;
    border: 2px solid #39ff14;
    padding: 1rem;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(57, 255, 20, 0.2);
    transition: transform 0.3s ease;
}

    .project-card:hover {
        transform: scale(1.05);
        box-shadow: 0 0 15px rgba(57, 255, 20, 0.4);
    }

footer.footer {
    text-align: center;
    padding: 2rem;
    font-size: 0.85rem;
    background: #111;
    border-top: 2px solid #39ff14;
    margin-top: 2rem;
}

a {
    color: #39ff14;
}

    a:hover {
        text-decoration: underline;
    }
